body {
  margin: 0;
  padding: 24px;
  font-family: sans-serif;
}

p {
  line-height: 1.5rem;
}

p:first-of-type {
  margin-top: 0;
}

p:last-of-type {
  margin-bottom: 0;
}

pre {
  float: left;
  width: 40%;
  padding-left: 12px;
  white-space: pre-wrap;
}

.Example-retained-rect {
  position: absolute;
  background-color: #FFF794;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  mix-blend-mode: multiply;
  pointer-events: none;
}

.Example-removed-rect {
  position: absolute;
  background-color: red;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  mix-blend-mode: multiply;
  pointer-events: none;
  opacity: 0.15;
}

.Example-added-rect {
  position: absolute;
  background-color: #FFF794;
  /*background-color: #B2E8B2;*/
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  mix-blend-mode: multiply;
  pointer-events: none;
}

.TextSelection *::-moz-selection {
  background-color: transparent;
}

.TextSelection *::selection {
  background-color: transparent;
}

.TextSelection *::-moz-selection {
  background-color: transparent;
}

.Knob {
  position: absolute;
  border: 1px solid #E04746;
  left: -4px;
  width: 7px;
  height: 7px;
  border-radius: 14px;
  position: absolute;
  background-color: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: opacity 0.1s ease, -webkit-box-shadow 0.1s ease;
  transition: opacity 0.1s ease, -webkit-box-shadow 0.1s ease;
  -o-transition: opacity 0.1s ease, box-shadow 0.1s ease;
  transition: opacity 0.1s ease, box-shadow 0.1s ease;
  transition: opacity 0.1s ease, box-shadow 0.1s ease, -webkit-box-shadow 0.1s ease;
  pointer-events: none;
}

.Knob--visible {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 0.15s ease;
  -o-transition: opacity 0.15s ease;
  transition: opacity 0.15s ease;
}

.Boundary:focus {
  outline: none;
}

.Boundary:focus .Knob {
  -webkit-box-shadow:
    0 0 0 1px white,
    0 0 0 2px #4498DE;
          box-shadow:
    0 0 0 1px white,
    0 0 0 2px #4498DE;
}

.Boundary:focus .Knob,
.Knob--hover {
  margin-top: -1px;
  left: -5px;
  border-width: 2px;
  background-color: #FADBDD;
}

.Knob--active {
  background-color: #EB797B;
  border: 2px solid #DD342E;
  cursor: none;
}

.Boundary {
  position: absolute;
}

.Boundary-caret {
  width: 1px;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #E04746;
  pointer-events: none;
}

.Boundary-caret--dragging {
  pointer-events: none;
}

.BoundaryControl {
  left: -1px;
  width: 3px;
  cursor: col-resize;
  outline: none;
}



